@import url('https://fonts.googleapis.com/css2?family=GFS+Didot&family=GFS+Neohellenic:ital,wght@0,400;0,700;1,400&family=Shrikhand&display=swap');

/* font-family: 'GFS Didot', serif;
font-family: 'GFS Neohellenic', sans-serif;
font-family: 'Shrikhand', cursive; */

#selectCard {
    /* border: 5px solid slateblue; */
    height: 90vh;
}
#selectCard h1 {
    border-radius: 18px;
    font-size: clamp(3rem, 12vw, 12vw);
    color: #ffecdd;
    background-color: #f93700;
    margin: 40px 30px 0 30px;
}
#divTest {
    /* border: solid red; */
    display: flex;
    align-items: center;
    height: 500px;
}
.gucci {
    margin: 0 100px;
}
.divOne {
    border: 1px solid green;
    background-color: #ffecdd;
    height: 250px;
    width: 600px;
    position: relative;
    padding: 20px;
}
.divOne > p, ul {
    margin-block-start: 20px;
}
.divOne h3 {
    margin-top: -20px;
    font-size: 40px;
}
.divOne ul {
    display: flex;
    justify-content: center;
}
/* ---------------------------------- */
.mhwMobile {
    display: none;
}
.divTwo {
    border: 1px solid #d2691e;
    background-color: #ffecdd;
    height: 250px;
    width: 600px;
    position: absolute;
    margin-left: 130px;
    padding: 15px;
    z-index: 2;
    cursor:default;
}
.divTwo h1 {
    font-family: 'Shrikhand', cursive;
    font-size: 40px;
}
.mwhTxt > * {
    margin-block-start: 20px;
}
.mwhTxt ul {
    display: flex;
    justify-content: center;
}
.peek {
    color: royalblue;
    font-size: 13px;
    display: flex;
    justify-content: flex-end;
    margin-top: -10px;
}

/* ------------------------------------------- */
.wordDiv {
    background-image: url('../../images/word.png');
    background-size: contain;
    background-repeat: no-repeat;
    width: 500px;
    height: 600px;
}
.divThree {
    border: solid hotpink;
    background-color: #ffecdd;
    height: 200px;
    width: 500px;
    margin: 40px;
    position: relative;
}
/* ------------------------------------------------------- */
.selectPara{
    height: 100vh;
    /* border: dotted blue; */
}
.testyPara {
    height: 50vh;
}
/* --------------------------------------------------- */
/* imgs */
.gucciImg, .spaceImg {
    width: 500px;
}
.zenogre {
    width: 400px;
    margin-left: 200px;
    transform: scaleX(-1);
}
.miniLogo {
    width: 40px;
}
/* --------------------------------------------- */
@media (min-width: 280px) and (max-width: 844px) {
    #selectCard {
        /* border: 5px solid slateblue; */
        height: 80vh;
    }
    #selectCard h1 {
        /* border-radius: 18px; */
        color: #ffecdd;
    }
    #divTest {
        /* border: solid red; */
        display: flex;
        flex-direction: column;
        height: 450px;
    }
    .gucci {
        margin: 20px;
    }
    .gucciImg {
        width: 350px;
    }
    .divOne {
        border: 1px solid green;
        height: 350px;
        width: 350px;
    }
    /* MONSTER HUNTER - GUCCI COMPONENT */
    .divTwo {
        height: 370px;
        width: 350px;
        margin: 250px 0px;
    }
    .zenogre {
        display: none;        
    }
    .peek {
        display: none;
    }
    .mhwMobile {
        display: block;
        background-image: url('../../images/mhw2.png');
        background-size: 100%;
        height: 500px;
        width: 1100px;
        margin: -50px 490px 0 0;
    }
    /* WORD COMPONENT*/
    .wordDiv {
        /* border: solid orange; */
        background-image: url('../../images/word.png');
        background-size: contain;
        background-repeat: no-repeat;
        width: 385px;
        height: 220px;
    }
    .divThree {
        /* border: solid hotpink; */
        height: 200px;
        width: 360px;
        margin: 12px;
        padding: 5px;
        position: relative;
    }
}